<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      title: "",
      content: "",
    };
  },
  mounted() {
    var param = this.$route.params.id;
    const url = "https://post-it-7m1i.onrender.com/notes/" + param;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        this.postIt = data;
        this.content = data.content[0];
        this.title = data.title;
      });
  },
  methods: {
    ...mapActions(["updateNote"]),
    saveData() {
      this.$router.push("/");
    },
    UpdateNoteTitle() {
      var id = this.$route.params.id;
      const noteToUpdate = {
        id: id,
        title: this.title,
        content: this.content,
      };
      this.updateNote(noteToUpdate);
      this.saveData();
    },
  },
};
</script>

<template>
  <div class="row">
    <ul class="notes form">
      <li>
        <div class="lazur-bg">
          <form @submit.prevent="UpdateNoteTitle">
            <input
              v-model="title"
              class="title"
              placeholder="Add title"
              required
            />
            <textarea
              v-model="content"
              class="title"
              placeholder="Add content"
              required
            >
            </textarea>
            <div style="text-align: center">
              <button id="task">Add task</button>
            </div>
          </form>
        </div>
      </li>
    </ul>
  </div>
</template>
